#skills-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--sub-spacer);
}
.skills-wrapper {
    width: 90%;
    max-width: 50rem;
    padding: 0 .5rem;
    display: grid;
    justify-items: center;
    grid-template-columns: repeat(auto-fill, minmax(7rem, 1fr));
    gap: 1rem;
}
.skill {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 7rem;
    height: 7rem;
    border: 2px solid #DF29D3;
    border-radius: 1rem;
    background-image: linear-gradient(to bottom right, #341A31, #20101e)
}
.skill svg {
    width: 70%;
}

@media screen and (max-width: 500px) {
    .skills-wrapper {
        gap: 1rem .25rem;
    }
}