@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Inter';
  color: white;
  transition: .3s;
}
.App {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
:root {
  --main-spacer: 6rem;
  /* --sub-spacer: calc(var(--main-spacer/2)); */
  --sub-spacer: 2.25rem;
}
main {
  display: flex;
  flex-direction: column;
  gap: var(--main-spacer);
  background-image: radial-gradient(circle at 0 0, #180c13, #1f141a, #372931);
  padding: 5rem 0;
  /* min-height: 300vh; */
}

h1 {
  font-size: clamp(1.75rem, 6.5vw, 5.5rem);
  text-align: center;
}
h2 {
  font-size: clamp(1.5rem, 5vw, 4.5rem);
  text-align: center;
}

