#contact-section {
    display: flex;
    flex-direction: column;
    gap: var(--sub-spacer);
}
.contacts-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}
.contact {
    min-width: 13.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: .5rem;
    background-image: linear-gradient(to bottom right, #df29d3, #52214c);
    border-radius: 1rem;
    padding: 2rem;
    text-decoration: none;
}
.contact * {
    color: #20101e;
}
.contact svg {
    width: 5rem;
}
@media screen and (max-width: 500px) {
    .contacts-wrapper {
        flex-direction: column;
        gap: 2rem;
    }
    .contact {
        width: 90%;
    }
}